.tabbe2 {
  tr {
    display: flex;
  }
  tr:nth-of-type(even) {
    background: #eee;
  }

  td,
  th {
    display: flex;
    width: 9%;
    justify-content: center;
    align-items: center;
    padding: 10px;
    text-align: center;
    span {
      font-size: 12px;
    }
  }
  th {
    background: #b8d0e8;
    color: #020f64;
  }
  table {
    height: fit-content;
    thead {
      display: table;
      background: #eee;
      // background-image: linear-gradient(to right,  #eee , #eee  100%) !important;
      color: black;
      table-layout: fixed;
      width: 100%;
    }
    tbody {
      overflow-y: auto;
      display: block;
      max-height: 22em;

      tr:hover {
        animation: tdanimation 1s infinite;
        animation-direction: alternate;
        animation-delay: 0s;
        background-color: #6c757d75;
        span:hover {
          color: #17a2b8;
        }
      }
    }
  }
}

