.add-modal {
  max-width: 900px;

  .add-input {
    margin: 10px;
  }
  .specific-input,
  .add-div {
    // margin-left: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .half-input {
    width: 45%;
  }
  .input-red {
    background-color: red;
  }
  .personnel-information {
    border: solid;
    border-left: none;
    border-top: none;
    border-right: none;
    border-width: 2px;
    border-color: gray;
  }
}
.affiche-modal {
  p {
    .fas {
      margin-right: 10px;
    }
  }
  .footer {
    margin: 20px;
    display: flex;
    justify-content: space-around;
    .element {
      display: flex;
      flex-direction: column;
      align-items: center;
      span {
        font-size: 15px;
        color: gray;
      }
    }
  }

  .title-element {
    font-weight: bold;
  }
}
@media only screen and (max-width: 1050px) {
  .add-modal {
    max-width: 900px;
  }
}

.card {
  box-shadow: 2px 2px 8px 2px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.clients-table {
  tr {
    display: flex;
  }
  .value,
  .code {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  td,
  th {
    display: flex;
    width: 15%;
    justify-content: center;
    align-items: center;
    span {
      font-size: 14px;
    }
  }
  .big-part {
    width: 35%;
  }
  .medium-part {
    width: 20%;
  }

  height: fit-content;
  overflow-y: hidden;
  .table {
    height: fit-content;
    thead {
      display: table;
      // background-color: #add9e0;
      background-image: linear-gradient(
        to right,
        #0f2535,
        #08052b 100%
      ) !important;
      color: white;
      table-layout: fixed;
      width: 100%;
    }
    tbody {
      overflow-y: auto;
      display: block;
      max-height: 22em;

      tr:hover {
        animation: tdanimation 1s infinite;
        animation-direction: alternate;
        animation-delay: 0s;
        background-color: #6c757d75;
        span:hover {
          color: #17a2b8;
        }
      }
    }
  }
}

