.clients-table {
  tr {
    display: flex;
  }
  .value,
  .code {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  td,
  th {
    display: flex;
    width: 15%;
    justify-content: center;
    align-items: center;
    span {
      font-size: 14px;
    }
  }
  .big-part {
    width: 35%;
  }
  .medium-part {
    width: 20%;
  }

  height: fit-content;
  overflow-y: hidden;
  .table {
    height: fit-content;
    thead {
      display: table;
      // background-color: #add9e0;
      background-image: linear-gradient(
        to right,
        #0f2535,
        #08052b 100%
      ) !important;
      color: white;
      table-layout: fixed;
      width: 100%;
    }
    tbody {
      overflow-y: auto;
      display: block;
      max-height: 22em;

      tr:hover {
        animation: tdanimation 1s infinite;
        animation-direction: alternate;
        animation-delay: 0s;
        background-color: #6c757d75;
        span:hover {
          color: #17a2b8;
        }
      }
    }
  }
}

