@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
.App {
  display: flex;
  width: 100%;
  align-items: stretch;
}

body {
  font-family: "Poppins", sans-serif;
  background: #fafafa;
}

a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.sidebar {
  min-width: 250px;
  max-width: 250px;
  /* background: #020f64; */
  background-image: linear-gradient(-20deg, #0f2535, #08052b) !important;

  color: #fff;
  margin-left: -250px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  /* overflow: hidden !important;
    overflow-anchor: none;
    touch-action: auto;
    height: 100%; */
}
.list-unstyled {
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.sidebar.is-open {
  margin-left: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  min-height: 800px;
}

.sidebar-header {
  /* background: #020f64; */
  background-image: linear-gradient(-20deg, #0f2535, #08052b) !important;
  /* background-image: linear-gradient(-20deg,  white , #eee ) !important; */
}

.sidebar-header h3 {
  color: #fff;
  padding: 1em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.menu-open {
  /* background: #020f64; */
  /* background-image: linear-gradient(-20deg, #0f2535, #08052b) !important; */
  /* background-image: linear-gradient(-20deg, #b8d0e8, #fff) !important; */
  background-image: linear-gradient(-20deg, #b8d0e8, #08052b) !important;

  /* color: #34b0c8; */
  color: white;
}

.itemcolor {
  background-image: linear-gradient(-20deg, #b8d0e8, #08052b) !important;
}

.nav-item:hover {
  color: #34b0c8;
  /* background: #fff; */
  background-image: -webkit-gradient(linear, left top, right top, from(#eee), to(#eee));
  background-image: linear-gradient(to right, #eee, #eee);
}

.items-menu {
  color: #fff;
  /* background: #020f64; */
  /* background-image: linear-gradient(-20deg, #0f2535, #08052b) !important; */
  background-image: linear-gradient(-20deg, #b8d0e8, #08052b) !important;
}

li a.dropdown-toggle {
  position: relative;
  /* left: 60%;
  top: 10%; */
  width: 90%;
  display: flex;
  justify-content: space-between;
  display: inline-flex;
}

.sidebar-header > span {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}
.side-menu {
  height: calc(100vh - 130px);
  overflow-y: scroll;
}

.side-menu::-webkit-scrollbar {
  width: 10px;
}

.side-menu::-webkit-scrollbar-thumb {
  /* background: #020f64; */
  background-image: linear-gradient(-20deg, #0f2535, #08052b) !important;

  border-radius: 3px;
}

.side-menu::-webkit-scrollbar-thumb:hover {
  /* background: #020f64; */
  background-image: linear-gradient(-20deg, #0f2535, #08052b) !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
.content {
  padding: 20px;
  margin-left: 0;
  height: 100vh;
}

@media only screen and (max-width: 500px) {
  body {
    overflow: hidden;
  }

  .content.is-open {
    margin-left: 100%;
  }

  .sidebar.is-open {
    min-width: 100%;
    max-width: 100%;
    margin-left: 0;
    -webkit-transition: all 0.5s, height 0s;
    transition: all 0.5s, height 0s;
  }

  .sidebar.is-open > .sidebar-header span {
    display: unset;
  }

  li a.dropdown-toggle::after {
    display: inline-block;
    position: relative;
    left: 20%;
  }
}
